import React, { useState, useRef, useEffect } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import axios from 'axios'
import {
  Sticky,
  Menu,
} from 'semantic-ui-react'
import loadable from '@loadable/component'
import {
  filterCountriesByRegion,
  searchCountriesByName,
  filterCountriesBySubRegion,
} from '../reducers/countriesReducer'
import { getCountryNews } from '../reducers/countryNewsReducer'
import regions from '../regions'
import '../assets/css/App1.css'

const HeaderNav = loadable(() => import('../components/Header'))
const Country = loadable(() => import('./Country'))
const CountriesTable = loadable(() => import('../components/Table'))

const Countries1 = () => {
  const [geo, setGeo] = useState([])

  const dispatch = useDispatch()

  const state = useSelector((state) => state)

  const countriesFiltered = useSelector((state) => state.countries.filtered)

  const handleRegionClick = (reg) => {
    dispatch(filterCountriesByRegion(reg))
    if (reg.region === 'All') {
      dispatch(searchCountriesByName(''))
      window.localStorage.clear()
    }
  }

  const handleSubregionClick = (sub) => {
    dispatch(filterCountriesBySubRegion(sub))
  }

  const contextRef = useRef()

  const getGeo = async (country) => {
    const res = await axios.get(
      `https://nominatim.openstreetmap.org/search?q=${country}&polygon_geojson=1&format=json`
    )
    const selectedGeo = res.data[0]
    return setGeo(selectedGeo)
  }

  useEffect(() => {
    if (countriesFiltered.length === 1) {
      const country = countriesFiltered[0].name.common
      if (countriesFiltered[0].region === 'Antarctic') {
        dispatch(getCountryNews('Antarctic'))
      } else {
        dispatch(getCountryNews(country))
      }
      getGeo(country)
    }
  }, [countriesFiltered])

  const noMatch = countriesFiltered.length === 0

  return noMatch ? (
    <div id="ref" ref={contextRef}>
      <Sticky id="Sticky" context={contextRef}>
        <HeaderNav />
      </Sticky>
    </div>
  ) : (
    <div id="ref" ref={contextRef}>
      <Sticky id="Sticky" context={contextRef}>
        <HeaderNav />

        {countriesFiltered.length === 1 ? (
          <></>
        ) : (
          <>
            <Menu attached tabular widths={7}>
              {regions.map((r) => (
                <Menu.Item
                  key={r.id}
                  active={
                    state.countries.filter.region.region.toLowerCase() ===
                    r.region.toLowerCase()
                  }
                  onClick={() => handleRegionClick(r)}
                >
                  {r.region === 'All' ? 'All Regions' : r.region}
                </Menu.Item>
              ))}
            </Menu>
            {state.countries.filter.region.region.toLowerCase() !== 'all' ? (
              <Menu
                widths={state.countries.filter.region.subregions.length}
                attached
                tabular
              >
                {state.countries.filter.region.subregions.map((s) => (
                  <Menu.Item
                    key={s.id}
                    active={
                      state.countries.filter.subregion.toLowerCase() ===
                      s.name.toLowerCase()
                    }
                    onClick={() => handleSubregionClick(s.name)}
                  >
                    {s.name}
                  </Menu.Item>
                ))}
              </Menu>
            ) : (
              <></>
            )}
          </>
        )}
      </Sticky>
      {countriesFiltered.length === 1 ? (
        <Country geo={geo} />
      ) : (
        <CountriesTable />
      )}
    </div>
  )
}

export default Countries1
